import React, { useState } from "react";
import cx from "classnames";
import styles from "./Forms.module.scss";

enum Step {
  Step1 = "STEP_1",
  Step2 = "STEP_2",
  Step3 = "STEP_3",
}

export default function Section() {
  const [step, setStep] = useState(() => Step.Step1);
  const [data, setData] = useState(() => ({
    [Step.Step1]: {
      username: "123",
    },
  }));

  return (
    <section className={styles.Section}>
      <h2>Forms</h2>
      {data[Step.Step1] && (
        <form
          className={cx(styles.Step, step === Step.Step1 && styles.selected)}
        >
          <fieldset>
            <legend>Step1</legend>
            <div>
              <label>
                <span>Username</span>
                <input
                  name="username"
                  value={data[Step.Step1].username}
                  onChange={(e) =>
                    setData((data) => ({
                      ...data,
                      [Step.Step1]: {
                        ...data[Step.Step1],
                        username: e.target.value,
                      },
                    }))
                  }
                />
              </label>
            </div>
            <div>
              <button
                onClick={(e) =>
                  e.preventDefault() ||
                  setData((data) => ({
                    ...data,
                    [Step.Step2]: {
                      password: "345",
                    },
                  }))
                }
              >
                next
              </button>
            </div>
          </fieldset>
        </form>
      )}
      {data[Step.Step2] && (
        <form>
          <fieldset>
            <legend>Step2</legend>
            <div>
              <label>
                <span>Password</span>
                <input
                  name="password"
                  value={data[Step.Step2].password}
                  onChange={(e) =>
                    setData((data) => ({
                      ...data,
                      [Step.Step2]: {
                        ...data[Step.Step2],
                        password: e.target.value,
                      },
                    }))
                  }
                />
              </label>
            </div>
            <div>
              <button
                onClick={(e) =>
                  e.preventDefault() ||
                  setData((data) => ({
                    ...data,
                    [Step.Step3]: {},
                  }))
                }
              >
                next
              </button>
            </div>
          </fieldset>
        </form>
      )}
      {data[Step.Step3] && (
        <form>
          <fieldset>
            <legend>Step3</legend>
            <div>
              <label>
                <span>LoggedIn</span>
              </label>
            </div>
            <div>
              <button
                onClick={(e) =>
                  e.preventDefault() ||
                  setData((data) => ({
                    [Step.Step1]: {
                      username: "123",
                    },
                  }))
                }
              >
                logout
              </button>
            </div>
          </fieldset>
        </form>
      )}
    </section>
  );
}
